html {
    margin: 0 auto;
}

body {
    margin: 0;
    font-family: 'Roboto', sans-serif;
    background-color: #eee;
    min-height: 90vh;
    padding: 0 0 60px;
    position: relative;
}

h2 {
    margin: 1em;
    font-weight: 100;
}

h3 {
    font-weight: 100;
}

@media (min-width: 767px) {
    html {
        max-width: 450px;
        background-color: #333;
    }

    body {
        padding: 0 0 60px 0;
    }

    .bottomBar {
        position: fixed !important;
        width: 450px !important;
    }
}


